import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument, CollectionReference, DocumentData } from '@angular/fire/firestore';
import firebase from 'firebase';
import { CartItem, Order } from '../objects/cart.model';
import { Additional, OrderItem } from '../objects/food-drink-menu.model';

@Injectable({
  providedIn: 'root'
})
export class OrderService {

  constructor(private fs:AngularFirestore) { }

  getFoodTotalPrice(cart_item:CartItem){
    let total:number = 0;
    cart_item.food.orders.map((order:OrderItem)=>{
      total += order.additional.quantity*order.additional.price;
    }) 
    total += cart_item.food.price;
    return total
  }

  getOrderPrice(order:Order){
    let total:number = 0;
    order.cart_items.map((cart_item:CartItem)=>{
      cart_item.food.orders.map((order:OrderItem)=>{
        total += order.additional.quantity * order.additional.price
      })
      total   += cart_item.food.price;
    })
    return total;
  }

  getOrders(cid:string,filter_status:string,filter_start_date:Date,filter_end_date:Date,startAfterDoc:DocumentData,limit:number):AngularFirestoreCollection<Order>{ 
        
    if(!filter_end_date){
      filter_end_date = new Date();
    }else{ 
      filter_end_date    = new Date(filter_end_date);
    }

    if(!filter_start_date){
      filter_start_date = new Date(0);
    }else{
      filter_start_date  = new Date(filter_start_date);
    }

    let filter_status_operant:firebase.firestore.WhereFilterOp = '==';
    // --------------------  Eğer filitrede status yok ise yada HEPSİ ise  ---------------------------
    if(filter_status === undefined || filter_status === 'hepsi'){
      if(startAfterDoc){
        return this.fs.collection('channels').doc(cid)
                      .collection('orders',ref=>ref
                        .orderBy('created_at','desc')
                        .where('created_at','>',filter_start_date) 
                        .where('created_at','<',filter_end_date)
                        .startAfter(startAfterDoc)
                        .limit(limit)
                      )
      }else{
        return this.fs.collection('channels').doc(cid)
                      .collection('orders',ref=>ref
                        .orderBy('created_at','desc')
                        .where('created_at','>',filter_start_date) 
                        .where('created_at','<',filter_end_date).limit(limit)
                      )
      }
    }else{
      // -------------------- filitre status Belirtilmişse  ---------------------------
      return this.fs.collection('channels').doc(cid)
                    .collection('orders',ref=>ref
                        .orderBy('created_at','desc')
                        .where('created_at','>',filter_start_date) 
                        .where('created_at','<',filter_end_date) 
                        .where('status.status','==',filter_status).limit(limit)
                      )
    } 

  }

  orderReference(cid:string):CollectionReference<DocumentData>{
    return this.fs.collection('channels').doc(cid).collection('orders').ref;
  }

  updateOrderStatus(orderId:string,cid:string,last_status:any){
    let created_at  = firebase.firestore.FieldValue.serverTimestamp();
    let status      = {created_at:created_at,status:last_status}
    return this.fs.collection('channels').doc(cid).collection('orders').doc(orderId).update({status:{...status}})
  }

  getCutomerOrders(cid:string):CollectionReference<DocumentData>{
    return this.fs.collection('channels').doc(cid).collection('orders').ref
  }


  async cancelOrder(cid: string, order:Order) {
    let created_at              = firebase.firestore.FieldValue.serverTimestamp();
    let status                  = order.status;
    status.cancel_by_cutomer    = 'cancel_by_cutomer';
    await this.fs.collection('channels').doc(cid).collection('orders').doc(order.id).update(
                                            { status: { ...status } 
                                          }); 
  }
 

}
