import { Address , Account} from "src/app/objects/account.model"; 
import { FoodDrinkFood } from "./food-drink-menu.model";

export class CartItem {
    uid:string;
    created_at:any;
    food:FoodDrinkFood; 
}

export class Order {
    id:string;
    uid:string;
    created_at:any; 
    cart_items:CartItem[];
    shipping_address:Address;
    status  :{status:string,created_at:any,cancel_by_cutomer:any};
    account:Account;
}

export class OrderStatus {
    status =[{id:'degerlendiriliyor',value:'Değerlendiriliyor'},
            {id:'hazirlaniyor',value:'Hazırlanıyor'},
            {id:'gonderildi',value:'Gönderildi'},
            {id:'iptal_edildi',value:'İptal Edildi'},
            {id:'hepsi',value:'Hepsi'}]
    getValue(id:string) {
        let val = this.status.find(status=>{
            return status.id == id
        })
        return val.value
    }
}  