import { Brochure } from "./brochure.model";
import { FoodDrinkMenu } from "./food-drink-menu.model"

export class Message {
    id:string;
    type:string         = 'text';
    text:string         = '';
    image_srcs:string[] = []; 
    media_files:MediaFile[]; 
    brochure:Brochure;
    food_drink_menu:FoodDrinkMenu;
    created_at:any  =   null;
    uid:string      =   null;    
    call_back_id:number =   null; //CallBackTypes id
    choices:[{}]    = [{text:'',correct:false}];// {text:'question text',correct:'true||false',value:'1-10 arası gibi'}
    reader_count    = 0;
}

export class CallBackTypes{
    types=[
            {id:0,name:'Soru',icon:'msg-quiz.svg'},
            {id:1,name:'Beğen',icon:'msg-like.svg'},
            {id:2,name:'Beğendim -Normal- Beğenmedim',icon:'msg-1-0-1.svg'},
            {id:3,name:'Evet-Hayır',icon:'msg-yesno.svg'},
            {id:4,name:'Anket',icon:'msg-survey.svg'},
            {id:5,name:'Cevap yaz',icon:'msg-reply.svg'}
        ]
    getNameByID(id:number) {
        let name:string = 'Text Mesaj';
        this.types.forEach((type)=>{
            if(type.id==id){
                name = type.name
            }
        })
        return name;
    }

    getIconByID(id:number) {
        if(id==null)return 'msg.svg';
        let icon:string;
        this.types.forEach((type)=>{
            if(type.id==id){
                icon = type.icon
            }
        })
        return icon;
    }
}

export class FeedBack{
    f:number = null;
    checked:boolean[]=[];
    reply:string='';
    uid:string='';
}

export class MenuVisitor{
    uid:string=null;
    created_at:any;
}

export class MediaFile{
    url:string;
    type:string;
    name:string;
}