import { Injectable } from '@angular/core'; 
import { AngularFirestore, AngularFirestoreCollection } from '@angular/fire/firestore';
import firebase from 'firebase'; 
import { CartItem } from '../objects/cart.model';
import { Channel, ShippingType } from '../objects/channel.model';
import { FoodDrinkFood } from '../objects/food-drink-menu.model';
import { AuthService } from './auth.service';

@Injectable({
  providedIn: 'root'
})
export class CartService {
  constructor(public  fs:  AngularFirestore, private authService:AuthService) {}

  addToCart(cartItem:CartItem,cid:string){
    return this.fs.collection('channels').doc(cid).collection('carts').add({...cartItem})
  }

  getCart(cid:string,channel:Channel):AngularFirestoreCollection<CartItem>{
    let  uid      = this.authService.USER?.uid;
    if(!uid){
      if(window.localStorage.getItem('uid')){
        uid = window.localStorage.getItem('uid')
      }else{
        uid = this.fs.createId();
        window.localStorage.setItem('uid',uid)
      }
    }
    return this.fs.collection('channels').doc(cid).collection('carts',ref=>ref.where('uid','==',uid))
  }

  rejectFood(item:any,cid:string){
    this.fs.collection('channels').doc(cid).collection('carts').doc(item.id).delete();
  } 

  complateOrder(order:any,cid:string){
    return this.fs.collection('channels').doc(cid).collection('orders').add({...order})
  }

}