export class FoodDrinkMenu {
    id  :string;
    uid :string;
    title:string;
    subtitle:string;
    categories:FoodDrinkCategory[]=[];
}

export class FoodDrinkCategory {
    id:string;
    title:string;
    subtitle:string;
    foods:FoodDrinkFood[]=[];
}

export class FoodDrinkFood {
    id:string;
    cid:string;
    title:string;
    subtitle:string;
    price:number;
    description:string;
    images:FoodDrinkImage[]=[];
    additionals:Additional[]=[];
    orders:OrderItem[]=[];
}

export class FoodDrinkImage {
    id  :string='';
    url :string='';
    gsPath :string=''; 
    base64data:string;
    File:File;
}

export class Additional {
    id  :string='';
    name :string='';
    price :number;
    quantity:number = 0;
}

export class OrderItem {  
    additional :Additional;
}
